import { enquireScreen } from "enquire-js";
import React from "react";
import About from "./About";
import Banner from "./Banner";
import Blog from "./Blog";
import Footer from "./Footer";
import Carousel from "./frinxmachine/Carousel";
import FrinxMachine from "./frinxmachine/FrinxMachine";
import Timeline from "./frinxmachine/Timeline";
import Header from "./Header";
import JoinUs from "./JoinUs";
import "./less/global.less";
import Testimonial from "./Testimonial";

let isMobile;
enquireScreen(b => {
  isMobile = b;
});

const { location } = window;

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isMobile,
      show: !location.port
    };
  }

  componentDidMount() {
    enquireScreen(b => {
      this.setState({ isMobile: !!b });
    });
    if (location.port) {
      setTimeout(() => {
        this.setState({
          show: true
        });
      }, 500);
    }
  }

  render() {
    return (
      <div
        ref={d => {
          this.dom = d;
        }}
      >
        <Header isMobile={this.state.isMobile} />
        <Banner isMobile={this.state.isMobile} />
        <About isMobile={this.state.isMobile} />
        <JoinUs />,
        <FrinxMachine isMobile={this.state.isMobile} />
        <Timeline isMobile={this.state.isMobile} />
        <Carousel />
        <Testimonial />
        <Blog />
        <Footer isMobile={this.state.isMobile} />
      </div>
    );
  }
}
